h2 {
	font-family: Georgia;
	text-transform: none;
	margin-bottom: 20px;
}

.about-container {
	width: 800px;
	padding: 80px;
	margin: 0 auto;
}

.space {
	height: 2px;
	background-color: black;
	margin: 40px 0;
	opacity: 0.25;
}

.about-container img {
	width: 500px;
	height: auto;
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	    -ms-transform: translateY(-50%);
	        transform: translateY(-50%);

}

.image-container {
	width: 500px;
	height: 500px;
	overflow: hidden;
}




